@charset "utf-8";
html {
  font-size: 625%;
}
html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
body {
  font-size: .14rem;
  color: #e1ecee;
  background: url("../images/bg.png");
  background-size: 100% 100%;
}
html,
body,
div,
table,
ul,
li,
ol,
dl,
dd,
dt,
a,
img,
label,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}
input {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style: none;
}
strong {
  font-weight: normal;
}
em,
i {
  font-style: normal;
}
img {
  max-width: 100%;
  display: block;
}
textarea {
  resize: none;
}
form {
  margin: 0;
}
a {
  color: #edfdff;
}
a,
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}
a:hover,
a:focus {
  color: #2bc1bf;
}
.wrap {
  width: 100%;
  margin: 0 auto;
}
.art-wrap {
  max-width: 1200px;
  width: 80%;
  margin: 0 auto;
}
/* 自定义*/
.mr5 {
  margin-right: .05rem;
}
.mr25 {
  margin-right: .25rem;
}
.yellow {
  color: #e7c900;
}
.tYellow {
  color: #fc8c40;
}
.tGreenBlue {
  color: #2e9809;
}
.tGreen {
  color: #43b498;
}
.tLightYellow {
  color: #95c136;
}
.tRed {
  color: #e44527;
}
.tBlue {
  color: #5f96b7;
}
/* 头部 start */
.header {
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100%;
  height: 1.2rem;
  background: url("../images/header.png") no-repeat;
  background-size: 100% 100%;
}
.header .select {
  float: left;
  position: relative;
  width: 5.7rem;
  height: 0.8rem;
  line-height: 0.8rem;
  padding-left: 0.4rem;
  cursor: pointer;
}
.header .select .tit {
  display: inline-block;
  vertical-align: middle;
  width: 4.9rem;
  height: .7rem;
  font-size: 0.36rem;
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
}
.header .select #scroll_begin {
  display: inline;
}
.header .select #scroll_begin .spanMargin {
  margin-right: .2rem;
}
.header .select #scroll_end {
  display: inline;
}
.header .select .san {
  display: inline-block;
  vertical-align: middle;
  font-size: 0.38rem;
  color: white;
}
.header .select .sHide {
  position: absolute;
  left: 0;
  top: 0.92rem;
  width: 5.78rem;
  height: 2.8rem;
  padding: 0.1rem 0.08rem 0.2rem 0.25rem;
  background: #013777;
  display: none;
}
.header .select .sHide .sUl {
  height: 2.5rem;
}
.header .select .sHide .sUl li {
  width: 5.2rem;
  min-height: 0.42rem;
  line-height: normal;
  padding: .1rem 0;
  border-bottom: 1px solid #27548a;
  font-size: 0.18rem;
  padding-left: 10px;
}
.header .select .sHide .sUl li:last-child {
  border-bottom: none;
}
.header .select .sHide .sUl li:hover {
  background: #15498d;
  color: #ffda24;
}
.header .describe {
  float: left;
  position: relative;
  margin-left: .7rem;
  width: 8.4rem;
  width: 43%;
  height: 0.7rem;
}
.header .describe .leftImg,
.header .describe .rightImg {
  width: .1rem;
  height: .44rem;
  background: url("../images/header-su.png") no-repeat;
  background-size: 100% 100%;
}
.header .describe .leftImg {
  position: absolute;
  left: 0;
  top: .12rem;
}
.header .describe .rightImg {
  position: absolute;
  right: 0;
  top: .12rem;
}
.header .describe .dP {
  padding: .12rem .2rem 0 .2rem;
  line-height: .26rem;
  font-size: .16rem;
  color: #ffffff;
  height: .6rem;
  overflow: hidden;
}
.header .describe .dP li {
  min-height: .6rem;
}
.header .caseConfig {
  float: right;
  margin-top: .22rem;
  margin-right: .2rem;
}
.header a {
  display: block;
  min-width: 1rem;
  padding: 0 .1rem;
  height: .3rem;
  line-height: .32rem;
  border-radius: 3px;
  background: #0c5fba;
  font-size: 0.14rem;
  color: #ffffff;
}
.header a .config {
  vertical-align: middle;
  margin: 0 .1rem 0 .1rem;
  font-size: .2rem;
}
/* 头部 end */
/*尾部开始*/
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2.15rem;
  padding-left: 0.5%;
  background: url("../images/footer-bg.png") top center no-repeat;
  background-size: 100% 100%;
}
.footer .swiper-container {
  width: 100%;
  height: 100%;
}
.footer .swiper-wrapper {
  width: 100%;
  /* background: url("../images/index-time.png") left 150px repeat-x; */
  background: url("../images/index-time.png") left 1.68rem repeat-x;
}
.footer .swiper-slide {
  height: 2.15rem;
  /*  width: 3.9rem; */
  width: 3.6rem;
  padding-top: .36rem;
  cursor: pointer;
}
.footer .swiper-slide .swiperIn {
  position: relative;
  height: 100%;
  border-left: 1px solid rgba(255, 255, 255, 0.3);
}
.footer .swiper-slide .swiperIn .gan {
  position: absolute;
  left: -0.08rem;
  top: -0.09rem;
  width: .15rem;
  height: 1.54rem;
  background: url("../images/gan.png") no-repeat;
  background-size: 100% 100%;
  display: none;
}
.footer .swiper-slide .swiperIn .gan.gShow {
  display: block;
}
.footer .swiper-slide .swiperIn .setTime {
  position: absolute;
  left: 0;
  bottom: .08rem;
  font-size: .12rem;
  color: #ffffff;
}
.footer .swiper-slide .swiperIn .setTime .time1 {
  display: inline-block;
  margin-right: .2rem;
  /* width: .58rem; */
  width: .85rem;
  height: .25rem;
  line-height: .25rem;
  text-align: center;
  vertical-align: middle;
  background: url("../images/index-times.png") no-repeat;
  background-size: 100% 100%;
}
.footer .swiper-slide .swiperIn .yellow {
  display: none;
  /* width: 3.64rem; */
  width: 3.5rem;
  height: .08rem;
  margin-top: .07rem;
  background: url("../images/yellow.png") no-repeat;
}
.footer .swiper-slide .swiperIn ul {
  padding-top: .03rem;
}
.footer .swiper-slide .swiperIn li {
  margin-top: .08rem;
  height: .34rem;
  width: 3rem;
  box-sizing: border-box;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.footer .swiper-slide .swiperIn li.lActive {
  box-sizing: border-box;
  border: 1px solid #fff200;
}
.footer .swiper-slide .swiperIn li.lActive .xing {
  color: #fff200;
}
.footer .swiper-slide .swiperIn .haveCon {
  margin-top: .08rem;
  min-height: .34rem;
  line-height: .34rem;
  padding-left: .1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  font-size: .14rem;
  color: #ffffff;
}
.footer .swiper-slide .swiperIn .haveCon .xing {
  vertical-align: middle;
  margin-right: .06rem;
}
.footer .swiper-slide .swiperIn .haveCon:hover {
  color: #2bc1bf;
}
.footer .swiper-slide .swiperIn .holpPlace {
  background: none;
}
.footer .swiper-slide .swiperIn .addNew {
  margin-top: .08rem;
  min-height: .34rem;
  line-height: .34rem;
  padding-left: .1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  font-size: .14rem;
  color: #ffffff;
}
.footer .swiper-slide .swiperIn .addNew .xing {
  vertical-align: middle;
  margin-right: .06rem;
}
.footer .swiper-slide .swiperIn .addNew:hover {
  color: #2bc1bf;
}
.footer .swiper-slide .swiperIn .addNew .jia {
  vertical-align: middle;
  margin-right: .06rem;
  color: #c81326;
}
.footer .swiper-slide .swiperIn .sBlue {
  background: #0486bb;
}
.footer .swiper-slide .swiperIn .sBlue .bTime {
  display: inline-block;
  vertical-align: middle;
  width: .12rem;
  height: .12rem;
  background: url("../images/db_01.png") no-repeat;
  background-size: 100% 100%;
  margin-right: .06rem;
}
.footer .swiper-slide .swiperIn .sRed {
  background: #c81327;
}
.footer .swiper-slide .swiperIn .sRed .bFire {
  display: inline-block;
  vertical-align: middle;
  width: .09rem;
  height: .12rem;
  background: url("../images/db_02.png") no-repeat;
  background-size: 100% 100%;
  margin-right: .06rem;
}
/* 尾部 end */
.contain {
  height: 100%;
  padding-top: 1.2rem;
  overflow-y: auto;
}
.wrap-con {
  width: 100%;
  height: 100%;
  padding: 0 .15rem;
}
.a-btn {
  display: inline-block;
  height: .3rem;
  line-height: .3rem;
  padding: 0 .15rem;
  border-radius: 3px;
  background: #fff;
  color: #000;
}
.btn {
  height: .48rem;
  line-height: .46rem;
  margin: 0 .05rem;
}
.ser-btn {
  display: inline-block;
  height: .3rem;
  line-height: .32rem;
  padding: 0 .2rem;
  border-radius: 3px;
  margin: 0 .1rem;
  font-size: .14rem;
  background: #0f6cdf;
  color: #fff;
}
/* table start */
.tables {
  width: 100%;
}
.tables thead {
  height: .7rem;
  background: #0a5ab4;
  color: #fff;
  font-size: .24rem;
}
.tables th {
  padding: 10px 10px;
  text-align: center;
}
.tables tbody {
  font-size: .16rem;
}
.tables tbody tr {
  width: 100%;
  height: .6rem;
}
.tables td {
  padding: 10px;
  text-align: center;
}
.tables > tbody > tr:nth-child(even) {
  background: rgba(255, 255, 255, 0.15);
}
.tables .txt .tables-bt {
  display: block;
  max-width: 100%;
  color: #00e4ff;
  /*  .txt(); */
}
/* table end */
/* bootstrap初始化 start */
.form-control {
  display: inline-block;
  height: .3rem;
  line-height: .3rem;
  padding: .1rem;
  border-radius: 3px;
  border: none;
  box-shadow: none;
  color: #737882;
  font-size: .16rem;
}
.form-control:hover {
  outline: none;
}
.form-control:focus {
  border-color: none;
  outline: none;
  box-shadow: none;
}
.dropup,
.dropdown {
  display: inline-block;
}
.dropdown-toggle {
  height: .3rem;
  line-height: .28rem;
  padding: 0 .1rem;
  min-width: .4rem;
  border: none;
  text-shadow: none;
  color: #fff;
  background: rgba(255, 255, 255, 0.22);
}
.dropdown-toggle:hover,
.dropdown-toggle:focus {
  background: rgba(255, 255, 255, 0.22);
  color: #fff;
}
.dropup .caret {
  border-top: 6px dashed;
  border-top: 6px solid \9;
  border-bottom: 0;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
}
.dropdown-menu {
  min-width: 80px;
  border-radius: 0;
}
.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:focus {
  background: #eef1f7;
  color: #029be5;
}
.icheckbox_flat-blue,
.iradio_flat-blue {
  margin-right: 5px;
  margin-top: -4px;
}
.modal-dialog {
  width: 13rem;
}
.modal-content {
  background: #013879;
}
.modal-body {
  max-height: 8rem;
  overflow-y: auto;
  padding: .4rem;
}
.modal-body .body-txt {
  text-align: center;
  font-size: .18rem;
}
.modal-header {
  position: relative;
  border-bottom: 1px solid #416a9b;
}
.modal-header .modal-title {
  padding-left: .2rem;
  font-size: .24rem;
}
.modal-header .modal-title:after {
  content: '';
  position: absolute;
  left: .15rem;
  top: .18rem;
  display: inline-block;
  width: .08rem;
  height: .32rem;
  background: #4be8ff;
}
button.close {
  display: inline-block;
  width: .22rem;
  height: .22rem;
  line-height: .2rem;
  text-align: center;
  border-radius: 50%;
  text-shadow: none;
  opacity: 1;
  background: #fff;
  color: #0f4380;
}
.modal-footer {
  text-align: center;
  border-top: 1px solid #416a9b;
}
.modal-footer .btn {
  padding: 0 .4rem;
  border: none;
  color: #000;
  background: #fff;
}
.modal-footer .yes {
  background: #0f6cde;
  color: #fff;
}
/* bootstrap end */
